import React, { FC } from 'react';
import { Student } from '../../types';
import { HTMLTable, Button } from '@blueprintjs/core';
import styled from 'styled-components';

interface Props {
  students: Student[];
}

const StyledTable = styled(HTMLTable)`
  width: 100%;
`;

export const StudentTable: FC<Props> = ({ students }) => {
  return (
    <div>
      <StyledTable bordered condensed>
        <thead>
          <tr>
            <th>Nom</th>
            <th>Est prsent</th>
            <th>Note</th>
          </tr>
        </thead>
        <tbody>
          {students.map((student) => (
            <tr key={student.personName} className={student.isPresent ? 'bg-green' : 'bg-red'}>
              <td>{student.personName}</td>
              <td>{student.isPresent ? 'Oui' : 'Non'}</td>
              <td>{student.note}</td>
            </tr>
          ))}
        </tbody>
      </StyledTable>
    </div>
  );
};
