import * as React from 'react';
import './Layout.scss';

interface Props {
  LeftElement: React.ReactElement;
  leftTitle?: string;
  RightElement: React.ReactElement;
  rightTitle?: string;
}

export const Layout: React.FC<Props> = (props) => {
  const { LeftElement, leftTitle, RightElement, rightTitle } = props;
  return (
    <div className="container">
      <div className="container__left">
        {leftTitle && <h2 className="title">{leftTitle}</h2>}
        {LeftElement}
      </div>
      <div className="container__right">
        {rightTitle && <h2 className="title">{rightTitle}</h2>}
        {RightElement}
      </div>
    </div>
  );
};
