import React from 'react';
import { Label, HTMLSelect, FormGroup, Divider, Button } from '@blueprintjs/core';
import { useObserver } from 'mobx-react-lite';
import { useRootStore } from '../../store';
import { TimePicker } from '@blueprintjs/datetime';
import styled from 'styled-components';

const StyledSameRow = styled.div`
  display: flex;
  justify-content: space-around;
`;

const StyledResetButton = styled(Button)`
  display: block;
  width: 60%;
  margin: 0 auto;
  text-align: center;
`;

export const SettingsPanel = () => {
  const { userPreferenceStore, presenceStore } = useRootStore();

  const allPersons = () => {
    const teacherName = userPreferenceStore.teacherName || 'Choisir un enseignant';
    if (!presenceStore.fileIsLoaded) {
      return [teacherName];
    }

    const studentNames = presenceStore.presenceData.students.map((s) => s.personName);
    return [teacherName, ...studentNames];
  };

  const onStartClassMinuteChange = (e) => {
    if (!e?.target?.value) return;
    userPreferenceStore.startClassMinute = parseInt(e.target.value);
  };

  const onclassDurationHoursChange = (e) => {
    if (!e?.target?.value) return;
    userPreferenceStore.classDurationHours = parseInt(e.target.value);
  };

  const onToleranceChange = (e) => {
    if (!e?.target?.value) return;
    userPreferenceStore.toleranceMinutes = parseInt(e.target.value);
  };

  const onStartDateChange = (date: Date) => {
    presenceStore.startClassDate = date;
  };

  const onEndDateChange = (date: Date) => {
    presenceStore.endClassDate = date;
  };

  return useObserver(() => (
    <div>
      <FormGroup>
        <Label>
          Dbut du cours (minutes)
          <HTMLSelect
            fill
            onChange={onStartClassMinuteChange}
            value={userPreferenceStore.startClassMinute}
          >
            <option value="0">:00</option>
            <option value="10">:10</option>
            <option value="20">:20</option>
            <option value="30">:30</option>
            <option value="40">:40</option>
            <option value="50">:50</option>
          </HTMLSelect>
        </Label>
      </FormGroup>
      <FormGroup>
        <Label>
          Dure du cours
          <HTMLSelect
            fill
            onChange={onclassDurationHoursChange}
            value={userPreferenceStore.classDurationHours}
          >
            <option value="1">1 heure</option>
            <option value="2">2 heures</option>
            <option value="3">3 heures</option>
            <option value="4">4 heures</option>
          </HTMLSelect>
        </Label>
      </FormGroup>
      <FormGroup>
        <Label>
          Tolrance absence de quitter
          <HTMLSelect
            fill
            onChange={onToleranceChange}
            value={userPreferenceStore.toleranceMinutes}
          >
            <option value="5">5 minutes</option>
            <option value="10">10 minutes</option>
            <option value="15">15 minutes</option>
            <option value="20">20 minutes</option>
            <option value="30">30 minutes</option>
          </HTMLSelect>
        </Label>
      </FormGroup>
      <FormGroup>
        <Label>
          Logiciel de prsence
          <HTMLSelect fill disabled>
            <option>Microsoft Teams</option>
          </HTMLSelect>
        </Label>
      </FormGroup>
      <Divider />
      <FormGroup>
        <Label>
          Enseignant(e)
          <HTMLSelect
            fill
            onChange={(e) => userPreferenceStore.saveTeacher(e.target.value)}
            disabled={!presenceStore.fileIsLoaded}
          >
            {allPersons()?.map((person) => (
              <option key={person}>{person}</option>
            ))}
          </HTMLSelect>
        </Label>
      </FormGroup>
      {presenceStore.fileIsLoaded && (
        <div>
          <StyledSameRow>
            <FormGroup>
              <Label>
                Dbut du cours
                <TimePicker
                  showArrowButtons
                  value={
                    presenceStore.startClassDate || presenceStore.presenceData.startDatetimeClass
                  }
                  onChange={onStartDateChange}
                />
              </Label>
            </FormGroup>
            <FormGroup>
              <Label>
                Fin du cours
                <TimePicker
                  showArrowButtons
                  value={presenceStore.endClassDate || presenceStore.presenceData.endDatetimeClass}
                  onChange={onEndDateChange}
                />
              </Label>
            </FormGroup>
          </StyledSameRow>
          <StyledResetButton intent="primary" onClick={presenceStore.resetStore}>
            Rinitialiser
          </StyledResetButton>
        </div>
      )}
    </div>
  ));
};
