import { EventType, ParsedEventsData } from './../../types';
import { PresenceEvent } from '../../types';
import { Parser } from './parser';
import moment from 'moment';

class MicrosoftTeamsDataParser implements Parser {
  parseEvents = (rawEvents: string[][]): ParsedEventsData => {
    rawEvents.splice(0, 1); // Delete the first line (column line)
    return {
      presumedTeacher: rawEvents[0][0], // First person to connect
      events: rawEvents.map(this.toPresenceEvent),
    };
  };

  toPresenceEvent = (data: string[]) => {
    return {
      personName: data[0],
      event: this.findEventType(data[1].toLowerCase()),
      date: moment(data[2], 'DD/MM/YYYY  hh:mm:ss').toDate(),
    };
  };

  findEventType = (rawEvent: string) => {
    if (rawEvent.includes('quitt')) {
      return EventType.Left;
    }
    return EventType.Joined;
  };
}

export const microsoftTeamsDataProcessor = new MicrosoftTeamsDataParser();
