import { ParsedEventsData } from './../../types';
import { DataSource, PresenceEvent } from '../../types';
import { microsoftTeamsDataProcessor } from './microsoft-teams';

export interface Parser {
  parseEvents(rawEvents: string[][]): ParsedEventsData;
}

export const getParser = (source: DataSource): Parser => {
  if (source === DataSource.MicrosoftTeams) {
    return microsoftTeamsDataProcessor;
  }

  throw new Error('Unknown data source parser');
};
