import { PresenceStore } from './presence-store';
import { UserPreferenceStore } from './user-pref-store';
import { createContext, useContext } from 'react';

export class RootStore {
  readonly userPreferenceStore: UserPreferenceStore;
  readonly presenceStore: PresenceStore;

  constructor() {
    this.userPreferenceStore = new UserPreferenceStore(this);
    this.presenceStore = new PresenceStore(this);
  }
}

// Create unique instance of store
export const initialStore = new RootStore();
export const RootStoreContext = createContext(initialStore);
export const useRootStore = () => useContext(RootStoreContext);
