export enum DataSource {
  MicrosoftTeams,
}

export interface ParsedEventsData {
  events: PresenceEvent[];
  presumedTeacher: string;
}

export interface PresenceEvent {
  personName: string;
  event: EventType;
  date: Date;
}

export enum EventType {
  Joined,
  Left,
}

export type PresenceEventDictionary = {
  [personName: string]: PresenceEvent[];
};

export interface Student {
  personName: string;
  isPresent: boolean;
  note?: string;
}

export interface PresenceData {
  teacherName: string;
  startDatetimeClass: Date;
  endDatetimeClass: Date;
  students: Student[];
}
