import { Student, PresenceEvent, PresenceData, PresenceEventDictionary } from './../types';
import { RootStore } from './index';
import { observable, computed, action } from 'mobx';
import { groupBy } from '../utils';
import { transformToPresenceData } from '../utils/presence-utils';

export class PresenceStore {
  readonly rootStore: RootStore;

  constructor(rootInstance) {
    this.rootStore = rootInstance;
  }

  @observable rawEvents: PresenceEvent[] = [];

  @observable startClassDate?: Date;

  @observable endClassDate?: Date;

  @computed get presenceData(): PresenceData {
    const eventsDictionary: PresenceEventDictionary = groupBy(this.rawEvents, 'personName');
    const {
      teacherName,
      startClassMinute,
      classDurationHours,
      toleranceMinutes,
    } = this.rootStore.userPreferenceStore;

    const presenceData = transformToPresenceData(
      eventsDictionary,
      teacherName,
      startClassMinute,
      classDurationHours,
      toleranceMinutes,
      this.startClassDate,
      this.endClassDate
    );

    return presenceData;
  }

  @computed get fileIsLoaded() {
    return this.rawEvents.length > 0;
  }

  @action resetStore = () => {
    this.rawEvents = [];
    this.startClassDate = undefined;
    this.endClassDate = undefined;
  };
}
