import { RootStore } from './index';
import { observable, action } from 'mobx';

export class UserPreferenceStore {
  readonly rootStore: RootStore;

  constructor(rootInstance) {
    this.rootStore = rootInstance;
  }

  @observable startClassMinute: number = 10;
  @observable classDurationHours: number = 2;
  @observable toleranceMinutes: number = 20;
  @observable teacherName: string = localStorage.getItem('teacherName') || '';

  @action saveTeacher = (teacherName: string) => {
    if (teacherName) {
      this.teacherName = teacherName;
      localStorage.setItem('teacherName', teacherName);
    }
  };
}
