import React from 'react';
import { UploadExcel } from './UploadExcel';
import { getParser } from '../../utils/data-parser/parser';
import { DataSource } from '../../types';
import { useObserver } from 'mobx-react-lite';
import { useRootStore } from '../../store';
import { StudentTable } from './StudentTable';

export const PresenceTablePanel = () => {
  const { presenceStore, userPreferenceStore } = useRootStore();

  const onFileUploaded = (data: string[][]) => {
    const { parseEvents } = getParser(DataSource.MicrosoftTeams);
    const { events, presumedTeacher } = parseEvents(data);

    if (!userPreferenceStore.teacherName) {
      userPreferenceStore.saveTeacher(presumedTeacher);
    }

    presenceStore.rawEvents = events;

    // Reset value (hack)
    const inputFile = document.querySelector('.csv-input');
    if (inputFile) {
      (inputFile as any).value = '';
    }
  };

  return useObserver(() => {
    if (presenceStore.fileIsLoaded) {
      return <StudentTable students={presenceStore.presenceData.students} />;
    }

    return (
      <div className="presenceTablePanel">
        <UploadExcel onFileUploaded={onFileUploaded} />
      </div>
    );
  });
};
