import { Student, PresenceData } from './../types';
import { PresenceEvent, EventType, PresenceEventDictionary } from '../types';
import moment, { Moment } from 'moment';

export const transformToPresenceData = (
  eventsDict: PresenceEventDictionary,
  teacherName: string,
  classStartMinute: number,
  classDurationHours: number,
  toleranceMinutes: number,
  startClassDate?: Date,
  endClassDate?: Date
): PresenceData => {
  const teacherEvent = eventsDict[teacherName];

  const startDate = startClassDate ?? getClassStartDate(teacherEvent[0].date, classStartMinute);
  const startDateMoment = moment(startDate);

  const endDate =
    endClassDate ??
    startDateMoment.clone().add(classDurationHours, 'hours').subtract(classStartMinute, 'minutes');
  const endDateMoment = moment(endDate);
  const endDateWithTolerance = endDateMoment.clone().subtract(toleranceMinutes, 'minutes');

  // Remove teacher from event
  delete eventsDict[teacherName];

  const students = Object.keys(eventsDict)
    .map((studentName) =>
      mapToStudentData(eventsDict[studentName], endDateMoment, endDateWithTolerance)
    )
    .sort((a, b) => (a.personName > b.personName ? 1 : -1));

  return {
    teacherName,
    students,
    startDatetimeClass: startDateMoment.toDate(),
    endDatetimeClass: endDateMoment.toDate(),
  };
};

export const getClassStartDate = (startDate: Date, classStartMinute: number) => {
  const date = startDate.toLocaleDateString('en'); // MM/DD/YYYY
  // Round minutes to hour
  const hours = startDate.getHours(); //4
  const minutes = startDate.getMinutes(); //01
  const roundedHour = Math.round(hours + minutes / 60);
  const time = `${roundedHour}:${classStartMinute}`;
  return moment(`${date} ${time}`, 'MM/DD/YYYY hh:mm');
};

export const mapToStudentData = (
  studentEvents: PresenceEvent[],
  endDate: Moment,
  endDateWithTolerance: Moment
): Student => {
  // Check if student is absent
  const lastEvent = studentEvents[studentEvents.length - 1];

  let isPresent = lastEvent?.event === EventType.Joined || false;
  let note;
  // Check absence + endDate tolerance
  // ex. if class finish at 4pm, but we tolerate 20 min of leaving earlier => 3:40pm = end date with tolerance
  // students leaves at 3:45pm, it's fine we consider this as present. so 3:45 pm > 3:40 pm
  if (!isPresent) {
    isPresent = lastEvent.date >= endDateWithTolerance.toDate();
    const diffMinutes = endDate.diff(lastEvent.date, 'minutes');
    note = `L'tudiant(e) est parti(e) ${diffMinutes} minutes avant la fin du cours`;
  }

  return {
    isPresent,
    note,
    personName: lastEvent.personName,
  };
};
